.device-header {
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.device-header .device-title-container {
  align-items: center;
  display: flex;
}

.device-header .device-title {
  color: #000;
  align-items: center;
  height: 32px;
  margin: 0 12px 0 0;
  font-size: 18px;
  display: flex;
}

.device-dropdown-button {
  box-shadow: none;
  background: #f0f0f0;
  border: none;
  border-radius: 20px;
  align-items: center;
  height: 32px;
  padding: 4px 12px 4px 8px;
  display: flex;
}

.device-dropdown-button .device-icon-container {
  background: #3b82f6;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 24px;
  height: 24px;
  display: flex;
  position: relative;
}

.device-dropdown-button .device-icon-container .device-icon {
  color: #fff;
  font-size: 14px;
}

.device-dropdown-button .device-icon-container .status-indicator {
  align-items: center;
  display: flex;
  position: absolute;
  bottom: -5px;
  right: -5px;
}

.device-dropdown-button .device-name {
  color: #333;
  text-overflow: ellipsis;
  white-space: nowrap;
  max-width: 120px;
  margin-left: 8px;
  margin-right: 4px;
  font-size: 16px;
  font-weight: bold;
  overflow: hidden;
}

.device-dropdown-button .device-name.no-device {
  color: #999;
  font-style: italic;
  font-weight: normal;
}

.device-dropdown-button .dropdown-arrow {
  color: #666;
  font-size: 12px;
  font-weight: bold;
  transform: scaleY(.6);
}

.device-dropdown {
  background: #fff;
  border: 1px solid #eaedf1;
  border-radius: 8px;
  width: 430px;
  overflow: hidden;
  box-shadow: 0 10px 20px #00000005;
}

.device-dropdown .dropdown-header {
  justify-content: space-between;
  align-items: center;
  padding: 16px 16px 12px;
  display: flex;
}

.device-dropdown .dropdown-header .dropdown-title {
  color: #333;
  font-size: 16px;
  font-weight: bold;
}

.device-dropdown .device-list-item {
  cursor: pointer;
  padding: 5px 17px 9px 6px;
}

.device-dropdown .device-list-item.selected {
  background: #2b83ff14;
}

.device-dropdown .device-list-item.offline {
  cursor: not-allowed;
  opacity: .5;
}

.device-dropdown .device-list-item .device-item-content {
  align-items: center;
  display: flex;
}

.device-dropdown .device-list-item .device-item-content .device-item-icon-container {
  justify-content: center;
  align-items: center;
  width: 34px;
  height: 34px;
  margin-right: 12px;
  display: flex;
}

.device-dropdown .device-list-item .device-item-content .device-item-icon-container .device-item-icon {
  color: #666;
  font-size: 22px;
}

.device-dropdown .device-list-item .device-item-content .device-item-info {
  flex: 1;
}

.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-name {
  color: #333;
  font-size: 15px;
  font-weight: bold;
}

.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-status {
  align-items: center;
  margin-top: 4px;
  display: flex;
}

.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-status .status-badge {
  align-items: center;
  margin-right: 12px;
  display: flex;
}

.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-status .status-badge .status-text {
  color: #666;
  font-size: 12px;
}

.device-dropdown .device-list-item .device-item-content .device-item-info .device-item-status .device-id-container {
  color: #999;
  font-size: 12px;
}

.device-dropdown .device-list-item .device-item-content .current-device-indicator {
  color: #1890ff;
  margin-left: auto;
  font-size: 13px;
  font-weight: bold;
}

.device-dropdown .device-list-empty {
  text-align: center;
  color: #999;
  padding: 20px;
}

.status-dot {
  margin-right: 4px;
  font-size: 12px;
}

.status-divider {
  margin: 0 4px;
}
