.scrcpy-container {
  flex-direction: column;
  width: 100%;
  height: 100%;
  display: flex;
}

.scrcpy-container .ant-card {
  background: none;
  border: none;
  flex-direction: column;
  flex: 1;
  display: flex;
}

.scrcpy-container .ant-card .ant-card-head {
  display: none;
}

.scrcpy-container .ant-card .ant-card-body {
  flex-direction: column;
  flex: 1;
  padding: 0;
  display: flex;
}

.scrcpy-container .ant-card .ant-card-body .ant-row {
  flex: 1;
  margin: 0 !important;
}

.scrcpy-container .ant-card .ant-card-body .ant-row .ant-col {
  padding: 0 !important;
}

.scrcpy-container .ant-card .ant-card-body .ant-row .ant-col:first-child {
  flex-direction: column;
  flex: 1;
  display: flex;
}

.scrcpy-container .header-bar {
  -webkit-backdrop-filter: blur(10px);
  backdrop-filter: blur(10px);
  background: #ffffffe6;
  border: 1px solid #f5f5f5;
  border-top-left-radius: 12px;
  border-top-right-radius: 12px;
  grid-template-columns: 1fr auto 1fr;
  align-items: center;
  padding: 8px 16px;
  display: grid;
}

.scrcpy-container .header-bar .header-left {
  align-items: center;
  gap: 8px;
  display: flex;
}

.scrcpy-container .header-bar .header-left .ant-typography {
  color: #333;
  margin: 0;
  font-size: 14px;
}

.scrcpy-container .header-bar .header-left .anticon {
  color: #999;
  cursor: pointer;
  font-size: 14px;
}

.scrcpy-container .header-bar .header-left .anticon:hover {
  color: #666;
}

.scrcpy-container .header-bar .screen-info {
  text-align: center;
}

.scrcpy-container .header-bar .screen-info .ant-typography {
  color: #666;
  margin: 0;
  font-size: 14px;
}

.scrcpy-container .header-bar .header-right {
  justify-self: end;
  align-items: center;
  gap: 8px;
  display: flex;
}

.scrcpy-container .header-bar .header-right .ant-btn {
  border: none;
  justify-content: center;
  align-items: center;
  width: 16px;
  height: 16px;
  display: flex;
}

.scrcpy-container .header-bar .header-right .ant-btn .link-icon {
  font-size: 16px;
  line-height: 1;
}

.scrcpy-container .video-section {
  flex-direction: column;
  flex: 1;
  display: flex;
  position: relative;
}

.scrcpy-container .video-section .video-container {
  background-color: #f5f5f5;
  border-bottom-right-radius: 12px;
  border-bottom-left-radius: 12px;
  flex: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  display: flex;
  position: relative;
  overflow: hidden;
}

.scrcpy-container .video-section .video-container .canvas-wrapper {
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
}

.scrcpy-container .video-section .video-container canvas {
  box-shadow: none !important;
  border: none !important;
}

.scrcpy-container .video-section .video-container .empty-state {
  z-index: 1;
  text-align: center;
  flex-direction: column;
  align-items: center;
  gap: 16px;
  padding: 24px;
  display: flex;
  position: relative;
}

.scrcpy-container .video-section .video-container .empty-state .empty-state-icon {
  font-size: 48px;
  line-height: 1;
}

.scrcpy-container .video-section .video-container .empty-state .empty-state-text {
  color: #666;
  margin-bottom: 8px;
  font-size: 16px;
}

.scrcpy-container .video-section .video-container .empty-state .loading-spinner {
  margin-top: 16px;
}

.scrcpy-container .video-section .video-container .empty-state .loading-spinner .ant-spin {
  color: #1677ff;
}

.scrcpy-container .video-section .video-container .empty-state .ant-btn {
  border-radius: 20px;
  min-width: 120px;
  height: 40px;
  font-size: 16px;
}

.scrcpy-container .video-section .screen-info {
  background: #00000080;
  border-radius: 4px;
  padding: 4px 8px;
  position: absolute;
  bottom: 8px;
  left: 8px;
}

.scrcpy-container .video-section .screen-info .ant-typography {
  color: #fff;
  margin: 0;
}

@media (max-width: 576px) {
  .scrcpy-container .header-bar {
    grid-template-rows: auto auto auto;
    grid-template-columns: 1fr;
    gap: 8px;
    padding: 8px;
  }

  .scrcpy-container .header-bar .header-left, .scrcpy-container .header-bar .screen-info {
    text-align: center;
  }

  .scrcpy-container .header-bar .header-right {
    justify-content: flex-end;
    justify-self: center;
    width: 100%;
  }
}
