body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Noto Sans, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji;
  font-size: 14px;
}

.app-container {
  background-color: #f5f5f5;
  flex-direction: column;
  width: 100%;
  height: 100vh;
  display: flex;
}

.app-content {
  height: 100vh;
  overflow: hidden;
}

.app-grid-layout {
  height: 100%;
  display: flex;
}

.app-grid-layout .ant-row {
  flex-wrap: nowrap;
  flex: 1;
  width: 100%;
  height: 100%;
  display: flex;
}

.app-panel {
  background-color: #fff;
  border-radius: 0;
  height: 100%;
  transition: box-shadow .3s;
  overflow: hidden;
  box-shadow: 0 1px 2px #0000000d;
}

.app-panel:hover {
  box-shadow: 0 2px 8px #00000017;
}

.app-panel.left-panel {
  flex-direction: column;
  flex: none;
  width: 480px;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.app-panel.right-panel {
  border-radius: 0;
  flex: 1;
  overflow: hidden;
  box-shadow: -4px 0 20px #0000000a;
}

.panel-content {
  border-left: 1px solid #00000014;
  flex-direction: column;
  height: 100%;
  padding: 12px 24px 24px;
  display: flex;
  overflow: auto;
}

.panel-content.left-panel-content {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.panel-content.right-panel-content {
  border-radius: 0;
}

.panel-content h2 {
  color: #000;
  margin-top: 16px;
  margin-bottom: 12px;
  font-size: 18px;
}

.panel-content canvas {
  border: 1px solid #f0f0f0;
  border-radius: 4px;
  max-width: 100%;
  margin-top: 16px;
}

.command-form {
  flex-direction: column;
  height: 100%;
  display: flex;
  overflow: hidden;
}

.command-form .form-content {
  flex-direction: column;
  height: 100%;
  display: flex;
}

.command-form .command-input-wrapper {
  margin-top: 8px;
  margin-bottom: 25px;
}

.result-container {
  flex-direction: column;
  flex: 1;
  height: 100%;
  min-height: 0;
  display: flex;
  position: relative;
  overflow: hidden;
}

@media (max-width: 768px) {
  .app-container {
    height: auto;
    min-height: 100vh;
  }

  .app-grid-layout .ant-row {
    flex-wrap: wrap !important;
  }

  .app-panel {
    height: auto;
    min-height: 200px;
    margin-bottom: 16px;
    flex: 0 0 100% !important;
    width: 100% !important;
  }

  .app-panel:first-child, .app-panel:first-child .panel-content {
    border-radius: 20px;
  }

  .panel-content {
    padding: 12px;
  }

  .panel-content h2 {
    margin-bottom: 12px;
    padding-bottom: 6px;
    font-size: 16px;
  }

  .panel-content textarea {
    min-height: 100px;
  }
}

@media (min-width: 769px) and (max-width: 992px) {
  .app-panel {
    min-height: 300px;
    margin-bottom: 16px;
  }
}

.resize-handle {
  background-color: #f0f0f0;
  width: 2px;
  transition: background-color .2s;
}

.resize-handle:hover {
  background-color: #1677ff;
}
