/* src/component/playground/index.less */
body {
  margin: 0;
  font-family:
    -apple-system,
    BlinkMacSystemFont,
    "Segoe UI",
    "Noto Sans",
    Helvetica,
    Arial,
    sans-serif,
    "Apple Color Emoji",
    "Segoe UI Emoji";
  font-size: 14px;
}
.playground-container {
  width: 100%;
  height: 100%;
}
.playground-container.vertical-mode {
  height: inherit;
}
.playground-container.vertical-mode .form-part h3 {
  font-size: 14px;
  line-height: 1.6;
}
.playground-container .playground-header {
  padding: 10px 10px 30px;
}
.playground-container .playground-left-panel {
  width: 100%;
  background-color: #FFF;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  overflow-y: auto !important;
}
.playground-container .playground-left-panel .ant-form {
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  justify-content: center;
}
.playground-container .form-part h3 {
  margin-top: 0;
  margin-bottom: 12px;
  font-size: 18px;
}
.playground-container .form-part .switch-btn-wrapper {
  margin-left: 2px;
}
.playground-container .loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  box-sizing: border-box;
  padding: 20px 20px;
}
.playground-container .loading-container .loading-progress-text {
  text-align: center;
  width: 100%;
  color: #777;
  margin-top: 16px;
}
.playground-container .loading-container .loading-progress-text-tab-info {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.playground-container .loading-container .loading-progress-text-progress {
  height: 60px;
}
.playground-container .result-wrapper {
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  box-sizing: border-box;
  border-radius: 4px;
  flex: 1 1 auto;
  min-height: 0;
}
.playground-container .result-wrapper.result-wrapper-compact {
  padding: 0;
}
.playground-container .result-wrapper.vertical-mode-result {
  height: inherit;
  min-height: 300px;
  display: flex;
  flex-direction: column;
  flex: 1 1;
}
.playground-container .result-wrapper pre {
  display: block;
  word-wrap: break-word;
  white-space: pre-wrap;
}
.playground-container .result-wrapper .result-empty-tip {
  flex: 1 1;
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 200px;
  width: 100%;
  height: 100%;
  border: 1px solid rgba(0, 0, 0, 0.12);
  border-radius: 8px;
  color: rgba(0, 0, 0, 0.45);
  font-size: 14px;
}
.input-wrapper {
  box-sizing: border-box;
}
.input-wrapper .mode-radio-group-wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.input-wrapper .mode-radio-group {
  display: flex;
  align-items: center;
  height: 100%;
}
.input-wrapper .mode-radio-group .ant-radio-button-wrapper {
  height: 24px;
  padding: 0 8px;
  line-height: 24px;
  border-radius: 11px;
  margin-right: 8px;
  box-shadow: none;
  border: none;
  background-color: #F7F7F7;
  font-size: 12px;
}
.input-wrapper .mode-radio-group .ant-radio-button-wrapper:before {
  display: none;
}
.input-wrapper .mode-radio-group .ant-radio-button-wrapper:focus-within {
  outline: none;
}
.input-wrapper .mode-radio-group .ant-radio-button-wrapper.ant-radio-button-wrapper-checked {
  background-color: #2B83FF;
  border-color: #2B83FF;
  color: white;
}
.input-wrapper .mode-radio-group .ant-radio-button-wrapper.ant-radio-button-wrapper-checked:hover {
  color: #fff;
}
.input-wrapper .mode-radio-group .ant-radio-button-wrapper:hover {
  color: #2B83FF;
}
.input-wrapper .main-side-console-input {
  position: relative;
  margin-top: 10px;
}
.input-wrapper .main-side-console-input .main-side-console-input-textarea {
  height: 90px;
  border-radius: 12px;
  border: 1px solid rgba(0, 0, 0, 0.12);
  padding: 12px 16px;
}
.input-wrapper .ant-input {
  padding-bottom: 40px;
}
.input-wrapper .form-controller-wrapper {
  position: absolute;
  bottom: 8px;
  padding: 0 12px;
  left: 0px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 100%;
  box-sizing: border-box;
  align-items: flex-end;
  gap: 8px;
}
.input-wrapper .settings-wrapper {
  display: flex;
  flex-direction: row;
  gap: 2px;
  color: #777;
  flex-wrap: wrap;
}
.input-wrapper .settings-wrapper.settings-wrapper-hover {
  color: #3b3b3b;
}

/* src/component/player.less */
.player-container {
  width: 100%;
  height: 100%;
  max-width: 100%;
  max-height: 100%;
  padding: 8px;
  background: #fff;
  box-sizing: border-box;
  border: 1px solid #0000000F;
  border-radius: 8px;
  line-height: 100%;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  overflow: visible;
  min-height: 300px;
  position: relative;
}
.player-container .canvas-container {
  flex: 1 1;
  min-height: 200px;
  display: flex;
  justify-content: center;
  align-items: center;
  overflow: hidden;
  padding: 8px;
  margin-bottom: 12px;
  position: relative;
  background-color: #F5F5F5;
  border-radius: inherit;
}
.player-container .canvas-container canvas {
  max-width: 100%;
  max-height: 100%;
  box-sizing: border-box;
  display: block;
  margin: 0 auto;
  object-fit: contain;
}
.player-container .player-timeline-wrapper {
  width: 100%;
  height: 4px;
  flex: none;
  margin-bottom: 2px;
  position: relative;
}
.player-container .player-timeline {
  width: 100%;
  height: 4px;
  background: #666;
  position: relative;
  flex-shrink: 0;
}
.player-container .player-timeline .player-timeline-progress {
  transition-timing-function: linear;
  position: absolute;
  top: 0;
  left: 0;
  height: 4px;
  background: #2B83FF;
}
.player-container .player-tools-wrapper {
  width: 100%;
  height: 72px;
  flex: none;
  position: relative;
  padding-top: 15px;
  padding-bottom: 15px;
  box-sizing: border-box;
}
.player-container .player-tools {
  width: 100%;
  height: 42px;
  max-width: 100%;
  overflow: hidden;
  color: #000;
  font-size: 14px;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  flex-shrink: 0;
}
.player-container .player-tools .ant-spin {
  color: #333;
}
.player-container .player-tools .player-control {
  flex-grow: 1;
  display: flex;
  flex-direction: row;
  align-items: center;
  overflow: hidden;
}
.player-container .player-tools .status-icon {
  transition: 0.2s;
  width: 32px;
  height: 32px;
  border-radius: 8px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-shrink: 0;
  margin-left: 10px;
}
.player-container .player-tools .status-icon:hover {
  cursor: pointer;
  background: #F0f0f0;
}
.player-container .player-tools .status-text {
  flex-grow: 1;
  flex-shrink: 1;
  min-width: 0;
  overflow: hidden;
  position: relative;
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
}
.player-container .player-tools .title {
  font-weight: bold;
  line-height: 20px;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
  width: 100%;
}
.player-container .player-tools .subtitle {
  line-height: 20px;
  width: 100%;
  overflow: hidden;
  text-overflow: ellipsis;
  white-space: nowrap;
}
.player-container .player-tools .player-tools-item {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: center;
}

/* src/component/logo.less */
.logo img {
  height: 30px;
  line-height: 30px;
  vertical-align: baseline;
  vertical-align: -webkit-baseline-middle;
}
.logo-with-star-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.logo-with-star-wrapper .github-star {
  height: 22px;
}

/* src/component/blackboard.less */
.blackboard .footer {
  color: #aaa;
}
.blackboard ul {
  padding-left: 0px;
}
.blackboard li {
  list-style: none;
}
.blackboard .bottom-tip {
  height: 30px;
}
.blackboard .bottom-tip-item {
  max-width: 500px;
  color: #AAA;
  text-overflow: ellipsis;
  word-wrap: break-word;
}
.blackboard-filter {
  margin: 10px 0;
}
.blackboard-main-content canvas {
  width: 100%;
  border: 1px solid #888;
  box-sizing: border-box;
}

/* src/component/shiny-text.less */
.shiny-text {
  position: relative;
  display: inline-block;
  background-size: 300% auto;
  -webkit-background-clip: text;
  background-clip: text;
  color: transparent;
  font-weight: 600;
  letter-spacing: 0.5px;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.05);
  overflow: hidden;
  padding: 0 5px;
  animation: textGradient 8s ease infinite;
  background-image: linear-gradient(45deg, #2b83ff, #6a11cb, #2575fc, #4481eb);
}
.shiny-text::after {
  content: "";
  position: absolute;
  top: -10%;
  left: -150%;
  width: 120%;
  height: 120%;
  background: linear-gradient(90deg, rgba(255, 255, 255, 0) 0%, rgba(255, 255, 255, 0.1) 10%, rgba(255, 255, 255, 0.6) 50%, rgba(255, 255, 255, 0.1) 90%, rgba(255, 255, 255, 0) 100%);
  transform: skewX(-20deg) translateY(0);
  animation: shine var(--animation-duration, 5s) cubic-bezier(0.25, 0.1, 0.25, 1) infinite;
  z-index: 1;
  pointer-events: none;
}
.shiny-text.disabled {
  animation: none;
  background: #2b83ff;
  -webkit-background-clip: text;
  background-clip: text;
}
.shiny-text.disabled::after {
  animation: none;
  display: none;
}
@keyframes shine {
  0% {
    left: -150%;
    opacity: 0.7;
  }
  20% {
    opacity: 1;
  }
  80% {
    opacity: 1;
  }
  100% {
    left: 250%;
    opacity: 0.7;
  }
}
@keyframes textGradient {
  0% {
    background-position: 0% 50%;
  }
  50% {
    background-position: 100% 50%;
  }
  100% {
    background-position: 0% 50%;
  }
}
